import {fetchData, loadWAFData } from "../../../utils/helpers";

export const noPlants = async (headers, type, objectId) => {
  try {
    const ENOVIA_BASE_URL = process.env.REACT_APP_ENOVIA_BASE_URL;
    console.log("[All Plant Service] headers: ", headers);
    const WAFData = await loadWAFData();

    // const fetchDataWithUrl = async (url) => {
    //   return new Promise((resolve, reject) => {
    //     WAFData.authenticatedRequest(url, {
    //       method: "GET",
    //       headers,
    //       type: "json",
    //       onComplete: (response) => {
    //         console.log("Received response:", response);
    //         resolve(response); // Resolve the promise with the response data
    //       },
    //       onFailure: (error) => {
    //         console.error("Request failed:", error);
    //         reject(error); // Reject the promise with the error
    //       },
    //     });
    //   });
    // };

    // Iterate through all collab spaces and fetch data for each
    
      try {
        const collabspaceURL = `${ENOVIA_BASE_URL}/resources/v1/modeler/dslib/dslib:Library/search?$searchStr=NoClassification`;

        const collabspaceResponse = await fetchData(
            "GET",
            collabspaceURL,
            ""
        );
        const libraryId = collabspaceResponse.member?.find(
          (item) => item.title === "NoClassification"
        )?.id;

        console.log("[All Plant Service] Library id is:", libraryId);

        if (libraryId) {
            const libraryDataURL = `${ENOVIA_BASE_URL}/resources/v1/modeler/dslib/dslib:Library/${libraryId}?$mask=dslib:ExpandClassifiableClassesMask`;

            const classResponse = await fetchData(
                "GET",
                libraryDataURL,
                ""
            );

            const noClassChild = classResponse.member[0]?.ChildClasses?.member?.find(child => child.title === "No Class");
            const Classid = noClassChild?.id;
            if (Classid) {
                const classifyUrl =
                  "https://saasimplementationserverdev.azurewebsites.net/plantAssignment/classifyProductToClass";
             
                const classifybody = {
                  id: objectId, // Use objectId from Redux
                  type: type, // Use type from Redux
                  classes: [Classid],
                  mode: "classifyParent",
                };
             
                console.log("Classify API Body:", classifybody);
             
                try {
                  const classifyResponse = await fetchData(
                    "POST",
                    classifyUrl,
                    classifybody,
                   
                  );
                  console.log("Classify Response:", JSON.stringify(classifyResponse));
                } catch (error) {
                  console.error("Error in Classify API Call:", error);
                }
              }
        }
      } catch (error) {
        console.error("Error processing group:", error);
        throw error; // Throw the error to be handled by Promise.all
      }
  } catch (error) {
    console.error("[All Plant Service] Error occurred:", error);
    throw error; // Re-throw the error for upstream handling
  }
};
